
#app {
  width: 40%;
  margin: 0 auto;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.hashSelect {
  padding: 0.5rem;
  text-decoration: none;
  background: #999;
}
@media only screen and (min-width: 768px) and (max-width: 1024px){
#app {
    width: 80%;
    font-size: 1.5rem;
}
}



